<script>
import {SHARED_LOADING} from "@/store/shared.module";
import {ROUTE_NAME} from '@/constants/route.constants';
import isEmpty from 'lodash/isEmpty'

export default {
  name: "paysecure",
  data: () => ({
    routeName: ROUTE_NAME,
    isDataPassed: false,
    countDown: 1,
    secureObj: {
      Amount: '',
      MerchantCode: '',
      CustomerCode: '',
      PhoneNumber: '',
      Currency: '',
      BankCode: '',
      LanguageCode: '',
      SourceAccNo: '',
      TransactionId: '',
      Reference: '',
      Timestamp: '',
      ClientIP: '',
      Signature: '',
      ResponseURL: '',
      BackendURL: ''
    }
  }),
  methods: {
    countDownTimer() {
      if (this.countDown > 0) {
        setTimeout(() => {
          this.countDown -= 1
          this.countDownTimer()
        }, 1)
      } else {
        this.submit()
      }
    },
    submit() {
      this.$ga.event('DPay', 'Submit', 'Pending', 1)
      this.$store.dispatch(`${SHARED_LOADING}`)
      paymentForm.submit()
    },
    onSubmit () {
      console.log("after submit")
    }
  },
  created() {
    let obj = this.$route.query
    this.isDataPassed = !isEmpty(obj)
    this.secureObj = {
      Amount: obj.Amount,
      MerchantCode: obj.MerchantCode,
      CustomerCode: obj.CustomerCode,
      PhoneNumber: obj.PhoneNumber.padStart(11, '0'),
      Currency: obj.Currency,
      BankCode: obj.BankCode,
      LanguageCode: obj.LanguageCode,
      SourceAccNo: obj.SourceAccNo.padStart(11, '0'),
      TransactionId: obj.TransactionId,
      Reference: obj.Reference,
      Timestamp: obj.Timestamp,
      ClientIP: obj.ClientIP,
      Signature: obj.Signature,
      ResponseURL: obj.ResponseURL,
      BackendURL: obj.BackendURL,
      payment_url: obj.payment_url
    };
    if(this.isDataPassed){
      this.countDownTimer()
    }
  }
}
</script>

